import { environment } from '@ali/4ever-cangjie';
import { isKeyHotkey } from 'is-hotkey';
import { toggleSup, toggleSub } from '../actions';

const isSupHotKey = environment.IS_WINDOWS ? isKeyHotkey("mod+'") : isKeyHotkey('mod+.');
const isSubHotKey = environment.IS_WINDOWS ? isKeyHotkey('mod+;') : isKeyHotkey('mod+,');

export default function onKeyDown(event, editor, next) {
  if (isSupHotKey(event)) {
    event.preventDefault();
    return editor.run('onAction', toggleSup());
  }
  if (isSubHotKey(event)) {
    event.preventDefault();
    return editor.run('onAction', toggleSub());
  }
  return next();
}
